.App {
  overflow-y: hidden;
  overflow-x: hidden;
}

.app_body {
  display: flex;
  background-color: #ededed;
}

.app_body {
  display: flex;
  background-color: #ededed;
  height: 100vh;
  /* width: 100vw; */
  margin: 0;
}

@media screen and (min-width: 960px) {
  .App {
    display: grid;
    place-items: center;

    margin-right: auto;
    margin-left: auto;
  }
  .app_body {
    /* max-width: 960px;
    min-width: 900px; */
    width: 960px;
  }
}
